/* jshint esversion: 6 */
// store
const initState = {
    isLogin:'',
    menuList:[]
}
// reducer
export const reducer = (state=initState,action)=>{
    const {type,data} = action
    switch(type){
        case 'SET_LOGIN_STATUS':
        	// statestate
            return {
                ...state,
                isLogin:data
            }
        case 'SET_MENU_LIST':
            return {
                ...state,
                menuList:data
            }
        default:
            return initState
    }
}
